@import "https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;700&family=Roboto&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --pimary-bg-color: #f4f4f4;
  --secondary-bg-color: #686868;
  --primary-text-color: #111321;
  --secondary-text-color: #f4f4f4;
  --primary-switcher-color: #4b48db;
  --secondary-switcher-color: #5f6775;
  --primary-swither-border-color: #4b48db;
  --secondary-swither-border-color: #fff;
  --primary-header-border: rgba(0, 0, 0, .2);
  --secondary-header-border: rgba(244, 244, 244, .2);
  --primary-close-btn-border-color: #111321;
  --secondary-close-btn-border-color: #fff;
  --white-color: #f4f4f4;
  --components-color: #4440f6;
  --dark-color: #111321;
  --grey-color: #a8a8a8;
  --white-color: #fff;
  --weather-bg-color: #4440f6;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
  --main-font: Monrope, sans-serif;
}

.default-img {
  width: 60vw;
  height: 60vh;
  background-image: url("desktop@2x.3a8061de.png");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 55%;
  left: 60%;
  transform: translate(-50%, -50%);
}

.is-hidden-btn {
  display: none;
}

header {
