}

.arrows {
  width: 0;
  height: 0;
  cursor: pointer;
  caret-color: rgba(0, 0, 0, 0);
  animation: arrow-up 3s infinite;
  position: sticky;
  bottom: 10%;
  left: 70%;
}

.arrows:hover {
  animation-play-state: paused;
}

@media screen and (min-width: 768px) {
  .arrows {
    left: 85%;
  }
}

@media screen and (min-width: 1280px) {
  .arrows {
    left: 90%;
  }
}

.arrow-up {
  border-left: 20px solid rgba(0, 0, 0, 0);
  border-right: 20px solid rgba(0, 0, 0, 0);
  border-bottom: 20px solid var(--components-color);
  opacity: .9;
}

.arrow-up.arrow-up-bottom {
  bottom: 7vh;
}

@keyframes arrow-up {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  font-family: inherit;
}

footer, header, menu, nav, section {
  display: block;
}

body {
  background: var(--pimary-bg-color);
  margin: 0;
  font-family: Monrope, sans-serif;
  font-weight: 400;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

